.sort-switcher {
  display: inline-block;
  align-items: baseline; }
  .sort-switcher__select, .sort-switcher__label {
    font-size: calc(10px + 0.5vmin); }
  .sort-switcher__label {
    padding-right: 1ch;
    color: #575757; }
  .sort-switcher__select {
    outline: none;
    border: 2px solid #f3f3f3;
    padding-left: 0.5ch;
    padding-right: 0.5ch;
    margin-left: 0.5ch;
    margin-right: 0.5ch;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    color: #575757; }
    .sort-switcher__select:disabled {
      color: lightgray; }
