@import "../variables.scss";

.users-container {
    &__header-panel {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        
        & > * {
            margin: $medium-blocks-offset;
        }
    }

    &__content-panel {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin: auto;
    }

    &__loader {
        font-size: calc(#{$min-font-size} + 1vmin);
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin: auto;
    }
}