@import "../../../variables.scss";

$odd-color: white;
$even-color: rgb(245, 245, 245);

$unmanaged-odd-color: aliceblue;
$unmanaged-even-color: rgb(223, 232, 240);

.users-table {
    display: table;
    width: 100%;

    &__header {
        display: table-row;
        background-color: $odd-color;
    }

    &__row {
        display: table-row;
        background-color: $odd-color;

        &:nth-child(2n) {
            background-color: $even-color;
        }

        &_unmanaged {
            background-color: $unmanaged-odd-color;

            &:nth-child(2n) {
                background-color: $unmanaged-even-color;
            }
        }
    }

    &__cell {
        display: table-cell;    
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 13px;   
        padding-right: 13px;  
        overflow: hidden; 

        &_heading {
            border-bottom: 2px solid black;
            cursor: pointer;
        }

        &_sorted_asc::after {
            content: " ⏶";
        }
    
        &_sorted_desc::after {
            content: " ⏷";
        }
    }
}